<template>
  <div id="app">
    <div class="header">
    <span>新增招标</span>
    <div class="header-right">
      <el-button size="small" type="warning" @click="axios()">刷新</el-button>
    </div>
    </div>
    <el-divider></el-divider>
    <!-- 下方列表显示 -->
    <div class="table-bot">
    <el-table
      :data="tableData"
      border
      style="width: 100%"
      >
      <el-table-column
        prop="bh"
        label="项目编号">
      </el-table-column>
      <el-table-column
        prop="dw"
        label="申请单位">
      </el-table-column>
      <el-table-column
        prop="name"
        label="项目名称">
      </el-table-column>
      <el-table-column
        prop="jg"
        label="预算价格">
      </el-table-column>
      <el-table-column
        prop="yj"
        label="立项依据">
      </el-table-column>
      <el-table-column
        prop="xq"
        label="详情">
        <template slot-scope="scope">
        <span class="det" @click="model(scope.row.id)">{{scope.row.xq}}<span></span></span>
      </template>
      </el-table-column>
    </el-table>
     <el-pagination
    background
    :page-size=pagesize
    :current-page=pagenum
    :hide-on-single-page="pds"
    @prev-click = 'upPage'
    @next-click = 'nextPage'
    @current-change	= 'currentPage'
    layout="prev, pager, next"
    :total=totalcount>
  </el-pagination>
    </div>
<!-- 查看详情里的详情和新增的模态框 -->
<el-dialog
  :visible.sync="dialogVisible"
  width="1200px">
  <tenderXq :id=sysid ref="tenderXq"></tenderXq><!--模态框内容-->
  <span slot="footer" class="dialog-footer">
    <el-button @click="dialogVisible = false">取 消</el-button>
    <el-button type="primary" @click="Submission">提 交</el-button>
  </span>
</el-dialog>
  </div>
</template>
<script>
import tenderXq from './tenderXq'//查看详情内容
export default {
    components:{
      tenderXq
    },
    data(){
      return{
        dialogVisible:false,//摸太框开关
        pds:true,//分页判断1页的话不显示
        totalcount:null,//总页数
        pagenum:1,//第几页
        pagesize:10,//一页显示多少条
        sysid:null,//传给子组件查看详情的id
        tableData: [],//外层列表
      }
    },
    mounted(){
        this.axios();
    },
    methods:{
      axios(){//列表数据
        this.$axios.get(`/tender/getTenderProjectList?pageNum=${this.pagenum}&pageSize=${this.pagesize}`).then(res=>{
          console.log(res)
          if(res.data.code==200){
            var data = res.data.data;
              this.totalcount = Number(res.data.totalcount);
              this.tableData = [];
              for(let i = 0;i<data.length;i++){
                  this.tableData.push({
                    bh: data[i].projectNum,
                    dw:data[i].projectUnitId,
                    name: data[i].projectName,
                    jg:data[i].projectBudgetPrice,
                    yj:data[i].projectBasis,
                    id:data[i].sysId,
                    xq:'查看详情'
              })
              }
          }
          if(res.data.code==204){
              console.log('查询失败')
            }
        })
      },
      upPage(page){//上一页
        this.pagenum = page;
        this.axios();
      },
      nextPage(page){//下一页
        this.pagenum = page;
        this.axios();
      },
      currentPage(page){//点击数字
        this.pagenum = page;
        this.axios();
      },
      model(id){//打开模态框
        this.dialogVisible = true;
        this.sysid = id;
      },

      Submission(){//提交本级项目新增的数据
          var newarr = [];
          var newlist = this.$refs.tenderXq.addlist;
           newarr={
                tenderNum:newlist[0][0].keys,//招标编号
                tenderForUnit:newlist[0][1].keys,//申请单位
                tenderType:newlist[0][2].keys,//招标方式
                leaderOpinion:newlist[0][3].keys,//支队领导意见
                projectDepartmentOpinion:newlist[0][4].keys,//支队项目办意见
                constructionOpinion:newlist[0][5].keys,//建设单位意见
                tenderDesignUnit:newlist[0][6].keys,//设计单位
                drawNum:newlist[0][7].keys,//图纸标号
                projectBudgetPrice:newlist[0][8].keys,//预算价格
                source:newlist[0][9].keys,//资金来源
                tenderAgent:newlist[0][10].keys,//承办人
                tenderUnit:newlist[0][11].keys,//单位
                startTime:newlist[0][12].keys,//开始日期
                tenderAttachment:newlist[0][13].keys,//招标附件
                projectId:String(this.sysid),//关联项目主键的id
                }
          // for(let i = 0;i<newlist.length;i++){
          //     newarr.push({
          //       tenderNum:newlist[i][0].keys,//招标编号
          //       tenderForUnit:newlist[i][1].keys,//申请单位
          //       tenderType:newlist[i][2].keys,//招标方式
          //       leaderOpinion:newlist[i][3].keys,//支队领导意见
          //       projectDepartmentOpinion:newlist[i][4].keys,//支队项目办意见
          //       constructionOpinion:newlist[i][5].keys,//建设单位意见
          //       tenderDesignUnit:newlist[i][6].keys,//设计单位
          //       drawNum:newlist[i][7].keys,//图纸标号
          //       projectBudgetPrice:newlist[i][8].keys,//预算价格
          //       source:newlist[i][9].keys,//资金来源
          //       tenderAgent:newlist[i][10].keys,//承办人
          //       tenderUnit:newlist[i][11].keys,//单位
          //       startTime:newlist[i][12].keys,//开始日期
          //       tenderAttachment:newlist[i][13].keys,//招标附件
          //       projectId:String(this.sysid),//关联项目主键的id
          //       })
          // }
          var dataJson = JSON.stringify(newarr);
          console.log(dataJson)
          this.$axios.post('/tender/insertTender',dataJson).then(res=>{
            console.log(res)
            if(res.data.code==200){
              this.$message({
              message: res.data.msg,
              type: 'success'
            });
              this.dialogVisible = false;
              this.axios();//新增成功刷新列表
            };
            if(res.data.code==204){
              this.$message.error(res.data.msg);
            }
          })
      }
    }
}
</script>
<style scoped>
    .header{
      display: flex;
      justify-content: space-between;
      padding: 10px 0;
    }
    .header span{
      vertical-align: middle;
      line-height: 32px;
    }
    .header .header-right{
      vertical-align: middle;
    }
    #app .el-divider--horizontal{
      margin: 0;
    }
    .table-bot >>> .el-table .cell{
      text-align: center;
    }

    .demo-table-expand {
    font-size: 0;
  }
  .demo-table-expand label {
    width: 90px;
    color: #99a9bf;
  }
  .demo-table-expand .el-form-item {
    margin-right: 0;
    margin-bottom: 0;
    width: 50%;
  }
  .table-bot >>> .el-table tr{
    cursor: pointer;
  }
  .demo-drawer__footer{
    position: absolute;
    bottom: 10px;
    width: 100%;
    display: flex;
    justify-content: space-between;
  }

  .card-box{
    overflow:scroll;
    margin-bottom: 50px;
  }
  .card-box >>> .el-card__body{
    padding: 10px;
  }
  .card-box >>> .el-form-item{
    margin-bottom: 0;
  }
  .card-box >>> label{
    margin-bottom: 0;
  }
  .el-dialog__wrapper >>> .el-drawer__header{
    margin-bottom: 15px;
  }
  .el-dialog__wrapper >>> .el-drawer__body{
    overflow-y: auto;
  }

  #app >>> .el-table td{
      padding: 0;
    }
    .det{
      position: relative;
      display: inline-block;
      width: 100%;
      height: 100%;
      cursor: pointer;
      padding: 12px 0;
    }
    .det span{
      width: 0px;
      position: absolute;
      bottom: 10px;
      transform: translateX(-50%);
      left: 50%;
      transition: .5s all;
      display: inline-block;
      border-bottom: 1.5px solid red;
    }
    .det:hover{
      color: red;
    }
    .det:hover span{
      width: 60px;
    }

    .mar-top{
      margin-top: 15px;
    }

    /* 分页 */
  .table-bot >>> .el-pagination{
    display: flex;
    justify-content: center;
    margin-top: 10px;
  }
  /* 新增里的title */
  #app >>> .el-card__body{
      padding-top: 0;
    }
  .title{
      text-align: center;
      margin: 15px 0;
      display: block;
      font-size: 18px;
      font-weight: bold;
    }
</style>



