          style="width:79%;vertical-align: middle;"
        type="textarea"
        autosize
        placeholder="请输入内容"
        :value="item.isCaiGou">
      </el-input>
      </div>

      <div class="mar-top">
          <label for="" style="width:20%;vertical-align: middle;">项&nbsp;&nbsp;&nbsp;目&nbsp;&nbsp;&nbsp;&nbsp;名&nbsp;&nbsp;&nbsp;称:</label>
          <el-input
          disabled
          style="width:79%;vertical-align: middle;"
        type="textarea"
        autosize
        placeholder="请输入内容"
        :value="item.projectName">
      </el-input>
      </div>

      <div class="mar-top">
          <label for="" style="width:20%;vertical-align: middle;">支&nbsp;队&nbsp;领&nbsp;导意见:</label>
          <el-input
          disabled
          style="width:79%;vertical-align: middle;"
        type="textarea"
        autosize
        placeholder="请输入内容"
        :value="item.leaderOpinion">
      </el-input>
      </div>

      <div class="mar-top">
          <label for="" style="width:20%;vertical-align: middle;">支队项目办意见:</label>
          <el-input
          disabled
          style="width:79%;vertical-align: middle;"
        type="textarea"
        autosize
        placeholder="请输入内容"
        :value="item.projectDepartmentOpinion">
      </el-input>
      </div>

      <div class="mar-top">
          <label for="" style="width:20%;vertical-align: middle;">建 设 单 位意见: </label>
          <el-input
          disabled
          style="width:79%;vertical-align: middle;"
        type="textarea"
        autosize
        placeholder="请输入内容"
        :value="item.constructionOpinion">
      </el-input>
      </div>

      <div class="mar-top">
          <label for="" style="width:20%;vertical-align: middle;">内&nbsp;&nbsp;&nbsp;容&nbsp;&nbsp;&nbsp;摘&nbsp;&nbsp;&nbsp;&nbsp;要: </label>
          <el-input
          disabled
          style="width:79%;vertical-align: middle;"
        type="textarea"
        autosize
        placeholder="请输入内容"
        :value="item.projectContent">
      </el-input>
      </div>

      <div class="mar-top">
          <label for="" style="width:20%;vertical-align: middle;">预&nbsp;&nbsp;&nbsp;算&nbsp;&nbsp;&nbsp;价&nbsp;&nbsp;&nbsp;&nbsp;格: </label>
