          style="width:79%;vertical-align: middle;"
        type="textarea"
        autosize
        placeholder="请输入内容"
        :value="item.projectBasis">
      </el-input>
      </div>

      <div class="mar-top">
          <label for="" style="width:20%;vertical-align: middle;">承&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;办&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;人: </label>
          <el-input
          disabled
          style="width:79%;vertical-align: middle;"
        type="textarea"
        autosize
        placeholder="请输入内容"
        :value="item.promoterId">
      </el-input>
      </div>

      <div class="mar-top">
          <label for="" style="width:20%;vertical-align: middle;">单&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;位: </label>
          <el-input
          disabled
          style="width:79%;vertical-align: middle;"
        type="textarea"
        autosize
        placeholder="请输入内容"
        :value="item.projectUnitId">
      </el-input>
      </div>

      <div class="mar-top">
          <label for="" style="width:20%;vertical-align: middle;">开&nbsp;&nbsp;&nbsp;始&nbsp;&nbsp;&nbsp;日&nbsp;&nbsp;&nbsp;&nbsp;期: </label>
          <el-input
          disabled
          style="width:79%;vertical-align: middle;"
        type="textarea"
        autosize
        placeholder="请输入内容"
        :value="item.startTime">
      </el-input>
      </div>
      <div class="mar-top">
