          <el-input
          disabled
          style="width:79%;vertical-align: middle;"
        type="textarea"
        autosize
        placeholder="请输入内容"
        :value="item.projectAttachment">
      </el-input>
      </div>
          </el-card>
      </div>
<!-- 新增数据 -->
 <div style="width:48.5%;display:inline-block;vertical-align: top;margin-bottom:25px;" v-for="(items,indexs) in addlist" :key="indexs+'1'">
<el-card shadow="hover">
<div :style=height>
   <span class="title">新增招标</span>
   <div>
     <div v-for="(item,index) in items" :key="index" :class="index!=0?'mar-top':''">
    <label for="" style="width:20%;vertical-align: middle;">{{item.name}}</label>
    <el-input
    v-if="item.name!='开始日期'"
    style="width:79%;vertical-align: middle;"
  type="textarea"
  autosize
  placeholder="请输入内容"
  v-model=item.keys>
</el-input>
  <!-- 循环出来的日期选择器判断 -->
  <el-date-picker
  v-if="item.name=='开始日期'"
  style="width:79%;vertical-align: middle;"
  size="small"
      v-model="item.keys"
      value-format='yyyy-MM-dd'
      align="right"
      type="date"
      placeholder="选择日期"
      :picker-options="pickerOptions">
    </el-date-picker>
</div>
</div>
</div>
    </el-card>
   </div>
  <!--新增图片-->
<!-- <div style="width:48.5%;display:inline-block;vertical-align: top;margin-bottom:25px;">
     <el-card shadow="hover">
    <div :style=height  @click="add">
      <div style="position:absolute;top:50%;left:50%;transform: translate(-50%,-50%);">
      <i class="el-icon-plus"></i>
      新增招标
      </div>
    </div>
     </el-card>
     </div> -->
    </div>
    </div>
</template>
<script>
import addLeveXq from '../projectManagement/addLeveXq'
export default {
    components:{
      addLeveXq
    },
    props:["id"],
    data(){
      return{
        listData:null,
        height:{position:'relative',height:'715px',cursor:'pointer',overflowY: 'auto'},//style新增录入数据的高度
        addlist:[[//新增招标的列表
          {name:'招标编号',keys:'',},
          {name:'申请单位',keys:'',},
          {name:'招标方式',keys:'',},
          {name:'支队领导意见',keys:'',},
          {name:'支队项目办意见',keys:'',},
          {name:'建设单位意见',keys:'',},
          {name:'设计单位',keys:'',},
          {name:'图纸标号',keys:'',},
          {name:'预算价格',keys:'',},
          {name:'资金来源',keys:'',},
          {name:'承办人',keys:'',},
          {name:'单位',keys:'',},
          {name:'开始日期',keys:'',},
          {name:'招标附件',keys:'',}]],//新增时循环的列表
        pickerOptions: {
          disabledDate(time) {
            return time.getTime() > Date.now();
          },
          shortcuts: [{
            text: '今天',
            onClick(picker) {
              picker.$emit('pick', new Date());
            }
          }, {
            text: '昨天',
            onClick(picker) {
              const date = new Date();
              date.setTime(date.getTime() - 3600 * 1000 * 24);
              picker.$emit('pick', date);
            }
          }, {
            text: '一周前',
            onClick(picker) {
              const date = new Date();
              date.setTime(date.getTime() - 3600 * 1000 * 24 * 7);
              picker.$emit('pick', date);
            }
          }]
        },
      }
    },
    methods:{
      details(){
        this.$axios.get(`/project/getOneProjectInfo?sysId=${this.id}`).then(res=>{
          this.listData = res.data.data.benJiProjectList;
        })
        },
      //   add(){//新增数据
      //   this.addlist.push([
      //     {name:'招标编号',keys:'',},
      //     {name:'申请单位',keys:'',},
      //     {name:'招标方式',keys:'',},
      //     {name:'支队领导意见',keys:'',},
      //     {name:'支队项目办意见',keys:'',},
      //     {name:'建设单位意见',keys:'',},
      //     {name:'设计单位',keys:'',},
      //     {name:'图纸标号',keys:'',},
      //     {name:'预算价格',keys:'',},
      //     {name:'资金来源',keys:'',},
      //     {name:'承办人',keys:'',},
      //     {name:'单位',keys:'',},
      //     {name:'开始日期',keys:'',},
      //     {name:'招标附件',keys:'',}
      //   ])
      // },
    },
    mounted(){
      this.details();
    },
    watch:{
      id(){
        this.details();
      }
    }
}
</script>
<style scoped>
    /* 详情 */
  .mar-top{
      margin-top: 15px;
    }
    #app >>> .el-textarea.is-disabled .el-textarea__inner{
      background-color: white;
    }
    #app >>> .el-card__body{
      padding-top: 0;
    }
    .title{
      text-align: center;
      margin: 15px 0;
      display: block;
      font-size: 18px;
      font-weight: bold;
    }
</style>



